<template>
  <el-container class="container">
    <el-header class="header">
      <div id="a" v-if="login">
        <el-button @click="open" type="primary" round >登录</el-button>
        <el-button @click="register" round>注册</el-button>
      </div>
      <div id="a" v-if="login==false">
          <el-tag>欢迎您！{{user}}</el-tag>
      </div>
    </el-header>

    <el-container>
      <el-aside width="200px">Aside</el-aside>
      <el-main><music></music></el-main>
    </el-container>
  </el-container>

</template>

<script>
import music from "./music"
export default {
  name: "HelloWorld",
  data() {
    return {
      login : true,
      user : "axzyz",
      pass:""
    };
  },
      methods: {
      open() {
        this.$prompt('请输入6-20位英文字母、数字或者符号（除空格），且字母、数字和标点符号至少包含两种密码', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: !/^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$)([^\u4e00-\u9fa5\s]){6,20}$/,
          inputErrorMessage: '密码格式不正确'
        }).then(({ value }) => {
          if ((this.pass = localStorage.getItem('axy')) == value) {
            this.pass = "";
            localStorage.setItem('axy','');
            this.login = false;
            this.$message({
            type: 'success',
            message: '欢迎登录'  
          }); 
          } else {
            this.$message.error('密码错误');
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });       
        });
      },
      register() {
        this.$alert('此功能尚未开发，请联系作者！', '警告', {
          confirmButtonText: '确定',
          callback: action => {
            this.$message({
              type: 'info',
              message: `关闭: ${ action }`
            });
          }
        });
      }
    },
  beforeCreate() {
    localStorage.setItem('axy','zsh2001.02.25')
  },
  components: {
    music
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
  background: url("../assets/858a89ca417878579f07742646b24677.jpg") no-repeat 50% 50%;
  background-size: cover;
  margin: 0 auto;
}
#a{
width: auto;
height: auto;
float: right;
margin-top: 10px;
}
</style>



